.screen {
  padding: 84px 0 100px;

  &__header {
    padding: 16px 0;
    margin-bottom: 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    h2 {
      margin: 0;
      font-size: 22px;
      font-weight: 100;
    }

    &__left,
    &__right {
      display: flex;
      align-items: center;
      gap: 20px;
    }
  }
}
